import CyLoading from './src/CyLoading.vue'

// 定义插件对象
// let Load = {}

// Vue的install方法，用于定义vue插件
CyLoading.install = (Vue, options) => {
  // 如果存在loading 不重复创建DOM
  if (document.getElementsByClassName('.cy_load_status').length) {
    return false
  }

  // 创建一个VUE构造器
  const LTemp = Vue.extend(CyLoading)

  // 提供一个在页面上已存在的DOM元素作为Vue实例的挂载目标。
  // 在实例挂载之后，可以通过$vm.$el访问。
  // 如果这个选项在实例化时有用到，实例将立即进入编译过程。否则，需要显示调用vm.$mount()手动开启编译(如下)
  // 提供的元素只能作为挂载点。所有的挂载元素会被vue生成的dom替换。因此不能挂载在顶级元素(html, body)上
  // let $vm = new toastTpl({
  //  el: document.createElement('div')
  // })

  // 实例化VUE实例
  let $vm = new LTemp()

  // 此处使用$mount来手动开启编译。用$el来访问元素，并插入到body中
  let tpl = $vm.$mount().$el
  document.body.appendChild(tpl)

  // 在Vue的原型上添加实例方法，以全局调用
  Vue.prototype.$cyloading = {
    showLoading() {
      $vm.showLoading = true
    },
    hideLoading() {
      $vm.showLoading = false
    }
  }

  // Vue.component(CyLoading.name, CyLoading)
}
// 导出Load
export default CyLoading



